import React, {useState} from 'react'
import ReactPlayer from 'react-player'
import Draggable from "react-draggable";

function Mobile() {

  const [myStream, setMyStream] = useState<MediaStream | null>(null);

  const playVideoFromCamera = async () => {
    try {
        const constraints = {'video': true, 'audio': true};
        const stream = await navigator.mediaDevices.getUserMedia(constraints);
        setMyStream(stream)
    } catch(error) {
        console.error('Error opening video camera.', error);
    }
  }

  useState(async () => {
    playVideoFromCamera()
  })

  return (
    <div className='h-screen'>
        <div className='flex flex-column items-center justify-center h-1/2 bg-black relative overflow-hidden'>
            
            <div className='flex justify-between items-center'>
              {myStream ? (
                  <ReactPlayer
                      className="react-player w-auto h-auto"
                      style={{transform: 'scaleX(-1)'}}
                      url={myStream}
                      playing={true}  
                      controls={false}
                      muted={true} 
                      playsinline={true} 
                  />
              ) : <>Loading</>}
            </div>

            <Draggable bounds="parent">
              <div className='absolute top-0 right-0 w-1/3 border-2 cursor-move'>
                {myStream ? (
                    <ReactPlayer
                        className="react-player w-auto h-auto"
                        style={{transform: 'scaleX(-1)'}}
                        url={myStream}
                        width='100%'
                        height='100%'
                        playing={true}  
                        controls={false}
                        muted={true}   
                        playsinline={true}
                    />
                ) : <>Loading</>}
              </div>
            </Draggable>

            

        </div>
        
        <div className='h-1/2 bg-white flex flex-col'>
            
            {/* Notifications */}
            <div className='w-screen h-1/10'>
                <div>
                  Hello !! This is Notification Center
                </div>
            </div>
            
            {/* Connect and Next Buttons */}
            <div className='w-screen h-1/10 flex'>
                <button className='w-1/2 bg-blue-400 font-medium text-white py-5 border-white border-4' >CONNECT</button>
                <button className='w-1/2 bg-blue-400 font-medium text-white py-5 border-white border-4 border-l-0' >NEXT</button>
            </div>

            {/* Chat Area */}
            <div className='flex-grow'></div>

            {/* Chat Input and Send Button  */}
            <div className='w-screen flex flex-row h-1/10'>
                <input className='w-2/3 p-4 border-2 flex-3' placeholder='Message'></input>
                <button className='w-1/3 bg-blue-400 font-medium text-white py-5 border-white border-4' >SEND</button>
            </div>
        </div>
    </div>
  )
}

export default Mobile